<template>
  <div>
    <!-- coloring button & wrappers -->
    <div class="color-show"></div>
    <div class="color-show"></div>
    <button
      class="color-changer"
      title="Change background"
      @click="expandColor"
    ></button>

    <h1>Welcome to Mockups !</h1>
    <p>Create your mockup for free &amp; Without the need of a graphical app</p>
    <h4>Instructions :</h4>
    <ul>
      <li>
        Upload the picture for the laptop mockup (Recommended Aspect Ratio is
        3:4)
      </li>
      <li>
        Upload the picture for the mobile mockup (Recommended Aspect Ratio is
        18:9)
      </li>
      <li>Screenshot & Share</li>
    </ul>
    more mockups will be added soon !
    <fieldset>
      <legend>Input your creation here</legend>
      <label for="laptopFile">Upload the laptop picture here </label>
      <label for="phoneFile">Upload the phone picture here</label>
      <input type="file" id="laptopFile" @change="readURL('laptop', $event)" />
      <input type="file" id="phoneFile" @change="readURL('phone', $event)" />
    </fieldset>

    <div class="canvas">
      <laptop class="show">
        <img id="laptop" src="" alt="choose file" />
      </laptop>
      <phone class="show">
        <img id="phone" src="" alt="choose file" />
      </phone>
    </div>
    <footer>
      <img src="./assets/logo.png" alt="logo" />
      <div>This Tool has been developed by <a href="">Moneer AlDakkak</a></div>
    </footer>
  </div>
</template>

<script>
import phone from "./components/phone.vue";
import laptop from "./components/laptop.vue";

export default {
  name: "App",
  components: {
    phone,
    laptop,
  },
  data() {
    return {
      i: 0,
      j: 0,
      a: 0,
      shownMenu: "bottom-left",
      placeholder: "<span>Type Something here</span>",
    };
  },
  methods: {
    expandColor() {
      let colors = [
        "#ffeaa7", //yellow
        "#d63031", //red
        "#3498db", //blue
        "#a29bfe", //violet
        "#27ae60", //green
        "#e84393", //pink
        "#fdcb6e", //orange
        "#c7ecee", //silver
        "#2c3e50", //dark blue
        "#00cec9", //cyan
        "#2d3436", //black
        "linear-gradient(133deg, rgba(255,52,109,1) 0%, rgba(241,140,71,1) 100%)", //po
        "linear-gradient(133deg, rgba(0,116,153,1) 0%, rgba(0,0,0,1) 100%)", //bb
        "linear-gradient(133deg, rgba(29,151,212,1) 0%, rgba(27,247,120,1) 100%)",
        "linear-gradient(40deg, rgba(255,178,35,1) 0%, rgba(255,250,124,1) 100%)",
      ];

      this.i++;
      this.j++;

      this.j > colors.length - 1 ? (this.j = 0) : this.j;

      if (this.i % 2 === 0) {
        document.querySelectorAll(".color-show")[0].classList.add("expanded");
        document
          .querySelectorAll(".color-show")[1]
          .classList.remove("expanded");
      } else {
        document.querySelectorAll(".color-show")[1].classList.add("expanded");
        document
          .querySelectorAll(".color-show")[0]
          .classList.remove("expanded");
      }

      document.body.style.background = colors[this.j - 1];

      document.querySelector(".expanded").style.background = colors[this.j];
    },
    readURL(chosenFile, input) {
      if (input.currentTarget.files && input.currentTarget.files[0]) {
        var reader = new FileReader();

        reader.onload = function (e) {
          document
            .getElementById(chosenFile)
            .setAttribute("src", e.target.result);
        };

        reader.readAsDataURL(input.currentTarget.files[0]);
      }
    },
  },
};
</script>

